<template>
  <div class="content">
    <div class="top-title"></div>
    <div v-if="isShow" class="mask-layer">
      <div class="content">
        <div style="font-size:14px;height:35rem;overflow-y: auto;padding: 1rem">
          <p style="font-weight:bold;"></p><p></p>
          <p>1.500</p><p>2.3</p><p>3.<3</p><p>4.tb</p><p>5.</p>
          <p></p>
          <p style="color:red;">-</p>
          <p style="color:red;"></p>
          <p style="color:red;">IPwifi</p>
          <p style="color:red;"></p>
          <p style="color:red;"></p>
          <p style="color:red;"></p>
        </div>
        <van-button class="btn" type="default" size="large" @click="continueGo">{{verifyText}}</van-button>
      </div>
    </div>

    <van-cell-group>
      <van-field v-model="user.name" label="" required placeholder=""/>
      <van-field v-model="user.mobile" label="" required placeholder=""/>
      <van-field v-model="user.taobaoUserId" label="" required placeholder=""/>
      <van-field
        label=""
        required
        v-model="user.bank"
        placeholder=""
        readonly
        @click="showBank = true"
      />
      <van-field v-model="user.bank_number" label="" required placeholder=""/>
      <van-field v-model="user.id_card" label="" required placeholder=""/>
      <van-cell-group>
        <!--confirm      
      cancel    
      change     Picker -->
        <van-field v-model="areaText" label="//" required @click="showArea = true" placeholder=""/>
        <van-popup v-model="showArea" position="bottom">
          <van-area
            ref="area"
            value="110000"
            :area-list="areaList"
            @change="onChangeAdd"
            @confirm="showArea = false"
            @cancel="showArea = false"
          />
        </van-popup>
      </van-cell-group>
      <van-field v-model="user.age" label="" required placeholder=""/>
      <van-field
        label=""
        required
        v-model="sexText"
        placeholder=""
        readonly
        @click="showSex = true"
      />
      <van-field
        v-model="user.password"
        label=""
        required
        type="password"
        placeholder="6"
      />
      <van-field
        v-model="user.repassword"
        label=""
        required
        type="password"
        placeholder=""
      />
      <mobileCode :mobile="user.mobile" v-bind:scene="`register`"  ref="mobileCode"/>
    </van-cell-group>
    <uploadNew v-if="isUpload" @setFiles = "setFiles" ref="uploadNew"/>
    <van-cell-group style="padding:10px;">
      <van-button :loading="isSubmit" @click="subRegister" type="danger" :bottom-action="true"></van-button>
    </van-cell-group>
    <van-popup v-model="showBank" position="bottom">
      <van-picker
        show-toolbar
        :columns="bankColumns"
        title=""
        @cancel="showBank = false"
        @confirm="onBankConfirm"
      />
    </van-popup>
    <van-popup v-model="showSex" position="bottom">
      <van-picker
        show-toolbar
        :columns="sexColumns"
        title=""
        @cancel="showSex = false"
        @confirm="onSexConfirm"
      />
    </van-popup>
  </div>
</template>
<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field,
  Area,
  Picker
} from "vant";

import { CONF, VERIFY_INVITE } from "@/api/user";
import { idCard } from "@/assets/js/regexp";
import uploadNew from "./upload-new";
import mobileCode from "@/common/mobileCode";
import bankList from "../../../assets/data/bank";
import areaLists from "../../../assets/data/area";
import { REGISTER, USER_SENDCODE, COMMON_CONF } from "@/api/user";
import validateMobile from "vant/lib/utils/validate/mobile";

export default {
  name: "regInvite",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview,
    [uploadNew.name]: uploadNew,
    [mobileCode.name]: mobileCode,
    [Area.name]:Area,
    [Picker.name]: Picker,
  },
  props: {
    invite_code: '',
    auth_time: '',
    auth_code: ''
  },
  created() {
    this.initPage()
  },
  data() {
    return {
      isUpload:true, // 
      isSubmit:false,
      showBank: false,
      showArea:false,
      showSex:false,
      sexText:'',
      bankColumns:[],
      sexColumns: [
        {
          value: "1",
          text: ""
        },
        {
          value: "2",
          text: ""
        },],
      user:{
        name: '',
        mobile: '',
        taobaoUserId:'',
        bank: '',
        bank_number: '',
        id_card:'',
        age:'',
        sex:'',
        address:'',
        password: '',
        repassword: '',
        v_code: ''
      },
      isShow: false,
      verifyText: '',
      countSeconds: 0,
      files:'',
      areaText: '',
      areaList: []
    };
  },
  methods: {
    initPage(){
      this.$reqGet(COMMON_CONF)
      .then(res => {
        let data = res.data.data;
        if(data.register_open != 1){
          Dialog.alert({
            title: "",
            message: "" // 
          }).then(() => {
            this.$router.push({
              name: "login",
            });
          });
        }
      }).catch((err) => {
      })
      if (!this.countSeconds) {
        this.countSeconds = 3;
        this.countDown();
      }
      this.bankColumns = bankList['bank_list']
      this.areaList = areaLists
      // 
      this.$reqPost(VERIFY_INVITE,{
        invite_code: this.invite_code
      }).then(res => {
        let data = res.data.data
        if(data.invite_auth != 1){
          Dialog.alert({
            title: "",
            message: ""
          }).then(() => {
              this.$router.push({
                name: "login",
              });
          });
        }
      }).catch((err) => {
      })
    },
    onChangeAdd (picker, value, index) {
      //areaText
      let areaName = ''
      let areaCodes = '';
      for (var i = 0; i < value.length; i++) {
        areaName = areaName + value[i].name + ' '
        areaCodes = areaCodes + value[i].code + ','
      }
      this.areaText = areaName
      this.user.address = areaCodes
    },
    onSexConfirm(value) {
      this.sexText = value.text;
      this.user.sex = value.value;
      this.showSex = false;
    },
    setFiles(file){
      this.files = file
    },
    //
    subRegister(){
      try {
          let _this = this;
          //
          let items = ["name","mobile",'taobaoUserId',"bank","bank_number","id_card","address","age","sex","invite_code","password","repassword"]
          items.forEach(function(item,index){
            let msg =_this.validate(item)
            if(msg && msg != ''){
              throw new Error(msg)
            }
          })

          let formdata = new FormData();
          if(this.isUpload){
            if(!this.files.upFile){
              throw new Error('')
            }
            if(!this.files.downFile){
              throw new Error('')
            }
            if(!this.files.alipayFile){
              throw new Error('')
            }
            if(!this.files.bankFile){
              throw new Error('')
            }
            if(!this.files.poseFile){
              throw new Error('')
            }
            formdata.append("front", this.files.upFile.file);
            formdata.append("reverse", this.files.downFile.file);
            formdata.append("pose", this.files.poseFile.file);
            formdata.append("alipayPic", this.files.alipayFile.file);
            formdata.append("bankPic", this.files.bankFile.file);
            formdata.append("poseEg", this.files.poseEgName);
          }

          formdata.append("name", this.user.name)
          formdata.append("mobile", this.user.mobile)
          formdata.append("taobaoUserId", this.user.taobaoUserId)
          formdata.append("bank", this.user.bank)
          formdata.append("bank_number", this.user.bank_number)
          formdata.append("card_no", this.user.id_card)
          formdata.append("password", this.user.password)
          formdata.append("sex", this.user.sex)
          formdata.append("age", this.user.age)
          formdata.append("address", this.user.address)
          formdata.append("areaText", this.areaText)
          formdata.append("v_code", this.$refs.mobileCode.v_code)

          formdata.append("invite_code", this.invite_code) //
          formdata.append("auth_time", this.auth_time) //
          formdata.append("auth_code", this.auth_code) //
          formdata.append("reg_type", 2) //
          this.isSubmit = true
          this.$reqPost(REGISTER, formdata).then(res => {
              this.isSubmit = false
              this.$router.push({ name: "follow"});
          }).catch(err => {
              this.isSubmit = false
          });
      } catch (e) {
        Toast(e.message)
      }
    },
    //
    validate(key) {
      const value = this.user[key];
      switch (key) {
        case "name":
          return value ? "" : "";
        case "mobile":
          if(!value) return "";
          return validateMobile(value) ? "" : "";
        case "taobaoUserId":
          return value ? "" : "";
        case "id_card":
           return idCard(value) ? '' : '1518';
        case "bank":
          return value ? "" : "";
        case "bank_number" :
          return value ? "" : "";
        case "address" :
          return value ? "" : "";
        case "age" :
          return value ? "" : "";
        case "sex" :
          return value ? "" : "";
        case "password" :
          return value ? "" : "";
        case "repassword":
          let msg = value ? "" : "";
          if (!msg) {
            msg = (value == this.user["password"]) ? "" : "";
          }
          return msg;
        case "v_code" :
          return value ? "" : "";
        default :
          return '';
      }
    },
    //
    onBankConfirm(value, index) {
      this.showBank = false;
      this.user.bank = value.text;
    },
    //
    goBack() {
      this.$router.push({name: 'index'});
    },
    countDown() {
      if (this.countSeconds === 0) {
        this.verifyText = "";
        return;
      } else {
        this.verifyText = `${this.countSeconds--}`;
      }
      setTimeout(() => {
        this.countDown();
      }, 1000);
    },
    continueGo() {
      if(this.countSeconds <=0 ){
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    },
    setVcode(code){
      this.user.v_code = code
    }
  }
};
</script>
<style lang="scss" scoped>
.top-title {
  text-align: center;
  padding: 12px;
  background-color: #fff;
  font-size: 16px;
}
.tip-r {
  color: red;
}
.tip_message{
  font-size: 12px;
}
.mask-layer {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.75);
  position: fixed;
  top: 0;
  z-index: 999;
  left: 0;
  .content {
    width: 80%;
    min-height: 80%;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &>div {
      box-sizing: border-box;
      padding: 20px 15px 0 15px;
      p {
        line-height: 1rem;
      }
    }
    .btn {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
</style>



